import React, { Component } from "react";
import ScatterBridge from "../Utils/ScatterBridge";
import { Header, PostWrapper } from "../Components";
import { PostContainer } from "../Containers";
import "./App.css";

class App extends Component {
  constructor(props) {
    super(props);

    this.network = {
        blockchain:'eos',
        chainId:'aca376f206b8fc25a6ed44dbdc66547c36c6c33e3a119ffbeaef943642f0e906',
        host: "api.eoseoul.io",
        port:443,
        protocol:'https'
    };

    this.scatter = new ScatterBridge(this.network, "EK Event");
    this.onClick = this.onClick.bind(this);
  }

  getTableUrl() {
    const url = this.network.protocol + "://" +
                this.network.host + ":" + 
                this.network.port + 
                "/v1/chain/get_table_rows"
    return url;    
  }

  async load() {
    const url = this.getTableUrl();
    let result = await fetch(url, {
      method: "POST",
      body: {
        code: "eosknightsio",
        table: "itemevt"
      }
    });


  }

  async onClick() {
    const scatter = this.scatter;
    await scatter.connect();
    await scatter.loadContract("eosknightsio");
    await scatter.contract.signup(scatter.account.name, scatter.auth);
  }

  render() {
    return (
      <div>
        <Header/>
        <PostContainer/>
      </div>
    );
  }
}

export default App;
